import imgKorona from '../../resources/korona.png'
import { useEffect, useState } from 'react';
import { Row, Col, Image, Button, Collapse, Accordion} from 'react-bootstrap';
import Datepicker from '../datepicker/Datepicker';
import { format, } from 'date-fns';
import { ru } from 'date-fns/locale';
import  useCovidService  from '../../service/CovidService';
import './datepickerBlock.scss'

function DatepickerBlock({setDateTo, setDateFrom, dateTo ,dateFrom}) {
    const {getMinMaxDate, loading, error} = useCovidService();
    const [baseMinDate, setBaseMinDate] = useState(null);
    const [baseMaxDate, setBaseMaxDate] = useState(null); 
    const [isVisibleFrom, setIsVisibleFrom] = useState(window.innerWidth > 768 ? true: false);
    const [isVisibleTo, setIsVisibleTo] = useState(window.innerWidth > 768 ? true: false);

    const [reset, onReset] = useState(false)
    const onBaseDate = () => {
        onReset(reset => (!reset))
    }

    useEffect(() => {
        const handleResize = () => {
            setIsVisibleFrom(window.innerWidth > 768);
            setIsVisibleTo(window.innerWidth > 768);
        };
        handleResize(); 

        window.addEventListener('resize', handleResize);  
        return () => window.removeEventListener('resize', handleResize); 
      }, []);

    useEffect(() => {
        getMinMaxDate()
            .then(res => { 
                setBaseMinDate(res.minDate)
                setBaseMaxDate(res.maxDate) 
                setDateTo(res.maxDate)
                setDateFrom(res.minDate)    
            })
   
    }, [reset])
   
    return (
        <Row>   
            <Col xl='8'>
                <h3 className='text-dark text-center mt-3'> </h3>
            </Col>
            <Col xl='4'></Col>
                <Col md='6' xl='4'className="d-flex justify-content-md-center flex-wrap">
                    <label className='text-center d-block w-100 fs-2'></label>
                    <Accordion className='w-100 date-button' defaultActiveKey="3" >
                        <Accordion.Item 
                            eventKey="1"
                            onClick={() => setIsVisibleFrom(isVisibleFrom => !isVisibleFrom)}
                            aria-controls="daypickerFrom"
                            aria-expanded={isVisibleFrom}>
                            <Accordion.Header>  {dateFrom ? format(dateFrom, 'PP', {locale: ru}) : null}</Accordion.Header>
                        </Accordion.Item>
                    </Accordion>
                    <Collapse in={isVisibleFrom}>
                        <div className="mx-auto bg-light rounded" style={{'height': '370px'}} >
                            <Datepicker
                                reset={reset}
                                baseMinDate={baseMinDate}
                                baseMaxDate={baseMaxDate}
                                setDateTo={setDateTo}
                                setDateFrom={setDateFrom}
                                select="min" 
                                loading={loading} 
                                error={error}/>  
                        </div>
                    </Collapse>
                </Col>          
                <Col md='6' xl='4' className="d-flex justify-content-md-center flex-wrap">
                    <label className='text-center d-block w-100 fs-2'></label>
                    <Accordion className='w-100 date-button' defaultActiveKey="0" >
                        <Accordion.Item 
                            eventKey="2"
                            onClick={() => setIsVisibleTo(isVisibleTo => !isVisibleTo)}
                            aria-controls="daypickerTo"
                            aria-expanded={isVisibleTo}>
                            <Accordion.Header>  {dateTo ? format(dateTo, 'PP', {locale: ru}): null}</Accordion.Header>
                        </Accordion.Item>
                    </Accordion>
                    <Collapse in={isVisibleTo}>
                    <div className="mx-auto bg-light rounded" style={{'height': '370px'}} >
                        <Datepicker 
                            reset={reset}
                            baseMaxDate={baseMaxDate}
                            baseMinDate={baseMinDate}
                            setDateTo={setDateTo}
                            setDateFrom={setDateFrom}
                            select="max" 
                            loading={loading} 
                            error={error}/>  
                    </div> 
                    </Collapse>
                </Col> 
                <Col xl='4' className="d-none d-xl-block position-relative">
                    <Image src={imgKorona} className='h-100 position-absolute '/>
                </Col> 
                <Col xl='8' className='d-flex justify-content-center p-3'>
                    <Button onClick={onBaseDate} id="start">  </Button>
                </Col>           
         </Row>          
    );
}

export default DatepickerBlock;